.table {
    width: 100%;
    text-align: center;
    font-size: 1.3rem;
    th:nth-child(1) {
      width: 33%;
    }
    th:nth-child(2) {
      width: 33%;
    }
    th:nth-child(3) {
      width: 33%;
    }
    td {
      padding: 1em 0;
    }
    th {
      padding: 2em 0;
    }
    .edit__btn,
    .remove__btn {
      color: inherit;
      text-decoration: none;
      display: inline-block;
      margin: 0 0.5em;
    }
    &__tbody {
      box-shadow: 0 0 0 2px #666; /* this draws the table border  */
      border-radius: 1em;
    }
  }